@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
    width: 100%;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 110px;
    width: 100%;
  }
}

.page {
  height: 100%;
  background-color: #ffffff;
}

.page__body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1920px;
  min-height: 100%;
  margin: 0;
  font-family: "Lato", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  font-style: normal;
  word-break: break-word;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .page__body {
    grid-template-rows: 1fr auto;
  }
}

@media (min-width: 1440px) {
  .page--inner .page__body {
    grid-template-rows: auto 1fr auto;
  }
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

@media (min-width: 1440px) {
  .page-header {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 5;
  }
}

.page--inner .page-header {
  position: relative;
}

.page-header__main-nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}
@media (max-width: 767px) {
  .page-header__main-nav {
    min-height: 66px;
  }
}
@media (max-width: 767px) {
  .page-header__main-nav {
    padding: 10px 0 18px;
  }
}
@media (min-width: 1440px) {
  .page-header__main-nav {
    padding-top: 53px;
  }
}

.logo {
  margin-right: 18px;
}

.logo--mobile {
  margin-right: 20px;
  width: calc(100% - 24px - 20px);
}
@media (min-width: 768px) {
  .logo--mobile {
    display: none;
  }
}

.logo__image {
  margin: 0;
  width: 34px;
  height: 38px;
}

.logo__inscription {
  position: absolute;
  left: 50%;
  bottom: 26px;
  margin: 0;
  display: block;
  width: 101px;
  height: 18px;
  transform: translateX(-50%);
}

.logo--tablet {
  display: none;
  width: 174px;
  height: 52px;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .logo--tablet {
    display: block;
  }
}

.logo--desktop {
  display: none;
  width: 202px;
  height: 59px;
}
@media (min-width: 1440px) {
  .logo--desktop {
    display: block;
  }
}

.logo:hover {
  opacity: 0.8;
}

.logo:active {
  opacity: 0.6;
}

.page-header-toggle {
  margin: 10px 0 0;
  padding: 8px 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.page-header__main-nav--opened .page-header-toggle {
  position: absolute;
  right: 4px;
  top: 23px;
  z-index: 11;
  margin: -8px 0 0;
  display: block;
  width: 20px;
  height: 20px;
}

.page-header-toggle__burger {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
@media (min-width: 768px) {
  .page-header-toggle__burger {
    display: none;
  }
}

.page-header__main-nav--opened .page-header-toggle__burger {
  width: 25px;
  transform: translate(-2px, 0) rotate(-45deg);
}

.page-header-toggle__burger::before {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translate(0, -7px);
}

.page-header__main-nav--opened .page-header-toggle__burger::before {
  width: 25px;
  transform: translate(0) rotate(90deg);
}

.page-header-toggle__burger::after {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translateY(5px);
}

.page-header__main-nav--opened .page-header-toggle__burger::after {
  display: none;
}

